.App {
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 2px;
  margin: 2px;
  
}
.shadowText {
  text-shadow: 4px 4px 10px crimson;
  color: white;
}

.shadowTextStrong {
  text-shadow: 4px 4px 10px orchid;
  color: orangered;
  font-size: medium;
  border-style: solid;
  border-radius: 15px;
  padding: 10px;
  border-color:  orangered;
  background-color: silver;
  box-shadow: 3px 3px 20px crimson;
}
.Title {
  flex-direction: row;
  color: crimson;
  padding-left: 8px;
  text-shadow: 5px 5px 15px orangered;
  font-family: 'Times New Roman', Times, serif;
}

li{
  text-align: left;
}
.Title:hover {
  transform: scale(1.1,1.1);
  color: crimson;
  text-shadow: 5px 5px 15px crimson;
}

.lang{
  display: flex;
  align-self: center;
  align-items: center;
  flex-direction: column;
}
.container{
  display: flex;
  flex-direction: column;
  align-self: center;
}

.Main{
  text-align: center;
  box-shadow: 3px 3px 50px #ffffff;
  display: flex;
  flex-direction: column;
  background: linear-gradient(#354080, rgb(31, 29, 129));
  font-family: 'Times New Roman', Times, serif;
  align-items: center;
  align-self: center;
  width: 90%;
  margin: 30px;
  border-width: 3px;
  border-style: solid;
  border-color: rgb(94, 29, 5);
  color: white;
  border-radius: 25px;
  padding: 2px;
}
.InfoBox {
  text-align: center;
  display: flex;
  flex-direction: column;
  color: rgb(94, 29, 5);
  background-color: white;
  align-self: center;
  width: 100%;
  border-color: rgb(94, 29, 5);
  border-radius: 10px;
  margin: 0;
  border-width: 3px;
  border-style: solid;
  padding: 0;
}

.InfoContainer {
  display: flex;
  flex-direction: column;
  margin: 10px;
  width: 100%;
  align-self: center;
}


.row {
  display: flex;
  flex-direction: row;
  padding: 8px;
}

button{
  background: linear-gradient(15deg, rgb(250, 58, 0), black);
  box-shadow: 5px 5px 10px rgb(65, 2, 2);
  border-radius: 20px;
  color: white;
  font-size: medium;
  margin: 2px;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
.mangoPic {
  border-radius: 50%;
  width: 160px;
  height: 200px;
  align-self: center;
}
.mangoPic:hover {
  box-shadow: 10px 10px 28px crimson;
}


body{
  color:  white;
  display: flex;
  flex-direction: column;
  /*background-image: url(./bg2.png);*/
  /*background-color: #1e3564;*/
  background-color: #3d3c5e;
  background-repeat: no-repeat;
  background-size: cover;
  
  font-family: 'Times New Roman', Times, serif;
  align-items: center;
  justify-content: center;
  padding: 0;
}

@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
  .App{
    width: 100%;
  }
  .container{
    width: 90%;
  }
  .row{
    display: flex;
    flex-direction: inherit;
  }
  .Main{
    width: 85%;
  }
  p{
    font-size: small;
  }
  }